.menu {
  background-color: $accent-2;
  &-background {
    width: 100%;
    max-height: 275px;
    object-fit: cover;
  }
  &-content {
    border-radius: 3rem 3rem 0 0;
    position: relative;
    // transform:translateY(-50px);
    margin-top: -50px;
    background-color: $accent-2;
    &--title {
      display: flex;
      flex-direction: row;
      justify-content: start;
      align-items: center;
      padding: 1.5rem;
      h2 {
        font-size: 28px;
        font-weight: 400;
        padding-right: 2rem;
      }
      @include respond-to("lg") {
        justify-content: center;
      }
    }
    &--dishList {
      padding: 1rem 1rem;
      opacity: 0;
      animation: fadeInUp 0.8s ease forwards;
      @include respond-to("lg") {
        max-width: 693px;
        margin: auto;
      }
      &_title {
        text-transform: uppercase;
        padding-bottom: 3px;
        font-size: 16px;
        font-weight: 300;
        border-bottom: 3px solid $accent-border;
        width: 40px;
        margin-bottom: 0.5rem;
      }
    }
    &--button {
      display: flex;
      justify-content: center;
      align-items: center;
      padding: 1.5rem;
      button {
        width: 190px;
      }
    }

    @include respond-to("lg") {
      width: 1055px;
      height: calc(100% + 500px);
    }
  }

  @include respond-to("lg") {
    background-color: $white;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
}

@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(50px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.entrees {
  animation-delay: 0.5s;
}

.plats {
  animation-delay: 1s;
}

.desserts {
  animation-delay: 1.5s;
}
